<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Header",
  computed: {
    ...mapGetters(["authenticated", "user"]),
  },

  methods: {
    ...mapActions(["signOut"]),

    signOutButton() {
      this.signOut()
      .then(() => {
        if(this.$router.path !== ('/')){
          this.$router.push('/')
        } 
      })
    },
  },
};
</script>

